// @ts-nocheck
import { Suspense, useEffect, useState } from 'react';
import { Html, OrbitControls, PerspectiveCamera, useProgress } from '@react-three/drei';
import { Canvas, useFrame } from '@react-three/fiber';
import { Vector3 } from 'three';
import { scene } from '@/store';
import { useWindowSize } from '@/hooks';

interface NavCameraProps {
    control: [number, number, number];
    position: [number, number, number];
    useMouse?: boolean;
}

const NavCamera = ({ control, position, useMouse }: NavCameraProps): any => {
    const controlVec = new Vector3(control[0], control[1], control[2]);
    const positionVec = new Vector3(position[0], position[1], position[2]);
    const standVec = new Vector3();
    const [time, setTime] = useState<any>(null);
    const [lookAt, setLookAt] = useState(true);
    const { isSmartphone, isTablet } = useWindowSize();

    const factor = isSmartphone || isTablet ? 2 : 3;

    useEffect(() => {
        if (scene.value === 'home') {
            clearTimeout(time);
            const debounce = setTimeout(() => setLookAt(true), 1000);
            setTime(debounce);
        } else {
            setLookAt(false);
        }
    }, [scene.value]);

    useFrame(({ controls, camera, mouse }) => {
        controls?.target?.lerp(controlVec, 0.18);
        camera.position.lerp(positionVec, 0.12);

        if (useMouse && lookAt) {
            standVec.set(mouse.x * factor, mouse.y * factor, camera.position.z * 1);
            camera.position.lerp(standVec, 0.4);
            camera.lookAt(0, 0, 0);
        }
    });
};

export const Scene = ({ children }: Component) => {
    const [position, setPosition] = useState<NavCameraProps['position']>([0, 1.5, 9]);
    const [control, setControl] = useState<NavCameraProps['control']>([0, 1.5, 9]);
    const [useMouse, setUseMouse] = useState<boolean>(true);
    const { isSmartphone, isTablet, isLaptop, isHorizontal } = useWindowSize();

    useEffect(() => {
        if (scene.value === 'home') {
            setPosition([0, 1.5, 9]);
            setControl([0, 0, 0]);
            setUseMouse(true);
        } else if (useMouse) {
            setPosition(
                isLaptop && isHorizontal
                    ? [0, 0, 80]
                    : isSmartphone
                    ? [0, 0, 80]
                    : isTablet
                    ? [0, 0, 60]
                    : [0, 1.5, 30],
            );
            setControl(
                isLaptop && isHorizontal
                    ? [-5, 3, 0]
                    : isSmartphone
                    ? [-5, 11, 0]
                    : isTablet
                    ? [-5, 7, 0]
                    : [-1.3, -0.55, 0],
            );
            setUseMouse(false);
        }
    }, [scene.value]);

    const Loader = () => {
        const { progress } = useProgress();
        return (
            <Html wrapperClass="loader" as="section">
                <p>LOADING...</p>
                <section className="progress_bar">
                    <h1>{`${Math.ceil(progress)}%`}</h1>
                    <span className="progress" style={{ width: `${progress}%` }} />
                </section>
            </Html>
        );
    };

    return (
        <Canvas className="canvas" shadows>
            <Suspense fallback={<Loader />}>
                <PerspectiveCamera
                    makeDefault
                    position={[0, 1.5, 9]}
                    fov={isLaptop && isHorizontal ? 5 : isSmartphone ? 18 : isTablet ? 16 : 5}
                />
                <OrbitControls
                    makeDefault
                    target={[0, 0, 0]}
                    enablePan={scene.value === 'home' ? false : true}
                    enableRotate={false}
                    enableZoom={false}
                />
                <pointLight position={[-1, 2.5, 1]} color="blue" intensity={300} power={500} />
                <pointLight position={[1, 2.5, -1]} color="pink" intensity={300} power={500} />
                <pointLight position={[0, 2.5, -1]} color="white" intensity={100} power={500} />
                <ambientLight intensity={1.5} />
                <NavCamera control={control} position={position} useMouse={useMouse} />
                {children}
            </Suspense>
        </Canvas>
    );
};
