import { AnimatePresence, motion } from 'framer-motion';
import { modalContent } from '@/store';
import './index.scss';
import { useEffect, useState } from 'react';
import { useWindowSize } from '@/hooks';

interface JobProps {
    job: CarrerItem;
}

export const Job = ({ job }: JobProps) => {
    const [trigger, setTrigger] = useState(true);
    const [time, setTime] = useState<any>(null);
    const { isSmartphone, isTablet } = useWindowSize();
    const { company, logo, period, roles, stack, text, website } = job;

    useEffect(() => {
        clearTimeout(time);
        setTrigger(false);

        const debounce = setTimeout(() => setTrigger(true), 100);

        setTime(debounce);
    }, [modalContent.value]);

    return (
        <AnimatePresence>
            {trigger && (
                <motion.section
                    className="job"
                    initial={isSmartphone || isTablet ? {} : { opacity: 0, y: -200 }}
                    animate={isSmartphone || isTablet ? {} : { opacity: 1, y: 0 }}
                    exit={isSmartphone || isTablet ? {} : { opacity: 0, y: 200 }}
                >
                    {isSmartphone || isTablet ? (
                        <div className="job-head">
                            <img src={`${logo}.jpg`} alt={`${company} logo`} />
                            <h2>{company}</h2>
                        </div>
                    ) : (
                        <>
                            <img src={`${logo}.jpg`} alt={`${company} logo`} />
                            <h2>{company}</h2>
                        </>
                    )}
                    <div className="job-items">
                        <div className="job-item">
                            <h4>Roles:</h4>
                            <span>{roles}</span>
                        </div>
                        <div className="job-item">
                            <h4>Period:</h4>
                            <span>{period}</span>
                        </div>
                        {website && (
                            <div className="job-item">
                                <h4>Website:</h4>
                                <a href={website} rel="noreferrer" target="_blank">
                                    {website}
                                </a>
                            </div>
                        )}
                    </div>
                    <div className="job-item">
                        <h4>Stack:</h4>
                        <ul>
                            {stack.map((item) => (
                                <li key={`${company}-${item}`}>{item}</li>
                            ))}
                            <li>and more</li>
                        </ul>
                    </div>
                    <p>{text}</p>
                </motion.section>
            )}
        </AnimatePresence>
    );
};
