import { useRef } from 'react';
import { Center, Svg } from '@react-three/drei';
import { useFrame } from '@react-three/fiber';
import { useWindowSize } from '@/hooks';

const path = '/rcf.svg';

export const Name = () => {
    const groupRef = useRef<any>(null);
    const { isSmartphone, isTablet, isHorizontal, isLaptop } = useWindowSize();
    const scale = isLaptop && isHorizontal ? 0.0008 : isSmartphone || isTablet ? 0.001 : 0.0008;

    useFrame(() => {
        groupRef.current.rotation.z += Math.PI / 300;
    });

    return (
        <group
            ref={groupRef}
            position={
                isLaptop && isHorizontal
                    ? [0, 0, -0.5]
                    : isSmartphone || isTablet
                    ? [0, 0.6, -0.5]
                    : [0, -0.04, -0.5]
            }
        >
            <Center>
                <Svg name="name" scale={[scale, scale, scale]} src={path} />
            </Center>
        </group>
    );
};
