import { effect, signal, Signal } from '@preact/signals-react';
import { ReactNode } from 'react';

// SCENE
export const scene = signal('home');
export const setScene = (s: string) => (scene.value = s);

// MODAL
export const isModalVisible = signal(false);
export const modalContent: Signal<ReactNode> = signal(null);
export const setModalContent = (content: Component['children']) => (modalContent.value = content);
export const closeModal = () => ((isModalVisible.value = false), (activeSkill.value = ''));
export const openModal = (content: Component['children']) => (
    (isModalVisible.value = true), (modalContent.value = content)
);
export const toggleModal = () => (isModalVisible.value = !isModalVisible.value);

// SKILLS
export const activeSkill = signal('');
export const setActiveSkill = (s: string) => (activeSkill.value = s);
