import { Html } from '@react-three/drei';
import { motion, AnimatePresence } from 'framer-motion';
import { AnimateChangeInHeight } from '@/components';
import { about } from '@/content';
import { useWindowSize } from '@/hooks';
import { scene } from '@/store';
import './index.scss';

export const About = () => {
    const { width, height, isHorizontal, isSmartphone, isTablet, isLaptop } = useWindowSize();
    const title1 = isSmartphone || isTablet ? 'DESIGN' : 'DESIGN & ';
    const title2 = isSmartphone || isTablet ? '& DEV' : 'PROGRAMMING';
    const title3 = 'DESIGN & DEV';

    const title = (
        <>
            {isHorizontal && (isSmartphone || isTablet || isLaptop) ? (
                <span style={{ display: 'flex' }}>
                    {title3.split('').map((char, index) => (
                        <motion.h1
                            key={`title3-${index}`}
                            variants={{
                                hidden: {
                                    y: -index * (height / 20),
                                    opacity: 0,
                                    x: -width / 3,
                                },
                                visible: { y: 0, opacity: 1, x: 0 },
                            }}
                            initial="hidden"
                            animate="visible"
                            transition={{
                                delay: 1 + index * 0.02,
                                duration: 1,
                                ease: 'anticipate',
                            }}
                        >
                            {index === 6 && <>&nbsp;</>}
                            {char}
                        </motion.h1>
                    ))}
                </span>
            ) : (
                <>
                    <span style={{ display: 'flex' }}>
                        {title1.split('').map((char, index) => (
                            <motion.h1
                                key={`title1-${index}`}
                                variants={{
                                    hidden: {
                                        y: -index * (height / 20),
                                        opacity: 0,
                                        x: -width / 3,
                                    },
                                    visible: { y: 0, opacity: 1, x: 0 },
                                }}
                                initial="hidden"
                                animate="visible"
                                transition={{
                                    delay: 1 + index * 0.02,
                                    duration: 1,
                                    ease: 'anticipate',
                                }}
                            >
                                {index === 6 && <>&nbsp;</>}
                                {char}
                            </motion.h1>
                        ))}
                    </span>
                    <span style={{ display: 'flex' }}>
                        {title2.split('').map((char, index) => (
                            <motion.h1
                                key={`title2-${index}`}
                                variants={{
                                    hidden: {
                                        y: index * (height / 20),
                                        opacity: 0,
                                        x: width / 3,
                                    },
                                    visible: { y: 0, opacity: 1, x: 0 },
                                }}
                                initial="hidden"
                                animate="visible"
                                transition={{
                                    delay: 1.25 + index * 0.02,
                                    duration: 1,
                                    ease: 'anticipate',
                                }}
                            >
                                {char}
                            </motion.h1>
                        ))}
                    </span>
                </>
            )}
        </>
    );

    return (
        <Html wrapperClass="about content" as="section">
            <AnimatePresence>
                {scene.value === 'about' && (
                    <motion.section
                        exit={{ x: width / 5, opacity: 0 }}
                        transition={{ duration: 0.33, ease: 'easeInOut' }}
                    >
                        <AnimateChangeInHeight
                            transition={{ delay: 0, duration: 1, ease: 'easeIn' }}
                        >
                            <motion.h2
                                variants={{
                                    hidden: { y: height / 9 },
                                    visible: { y: 0 },
                                }}
                                initial="hidden"
                                animate="visible"
                                transition={{ delay: 0, duration: 1, ease: 'anticipate' }}
                            >
                                {about.heading}
                            </motion.h2>
                        </AnimateChangeInHeight>
                        <AnimateChangeInHeight
                            transition={{ delay: 1, duration: 1, ease: 'easeInOut' }}
                        >
                            {title}
                        </AnimateChangeInHeight>
                        <AnimateChangeInHeight
                            transition={{ delay: 3.5, duration: 0.5, ease: 'easeInOut' }}
                        >
                            <motion.p
                                variants={{
                                    hidden: { y: -height / 2 },
                                    visible: { y: 0 },
                                }}
                                initial="hidden"
                                animate="visible"
                                transition={{
                                    duration: 0.75,
                                }}
                            >
                                {about.texts[0]}
                            </motion.p>
                        </AnimateChangeInHeight>
                        <AnimateChangeInHeight
                            transition={{ delay: 3.55, duration: 0.5, ease: 'easeInOut' }}
                        >
                            <p>{about.texts[1]}</p>
                        </AnimateChangeInHeight>
                        <AnimateChangeInHeight
                            transition={{ delay: 3.6, duration: 0.5, ease: 'easeInOut' }}
                        >
                            <motion.p
                                variants={{
                                    hidden: { y: height / 2 },
                                    visible: { y: 0 },
                                }}
                                initial="hidden"
                                animate="visible"
                                transition={{
                                    duration: 0.75,
                                }}
                            >
                                {about.texts[2]}
                            </motion.p>
                        </AnimateChangeInHeight>
                    </motion.section>
                )}
            </AnimatePresence>
        </Html>
    );
};
