// @ts-nocheck
import { useEffect, useRef, useState } from 'react';
import { ContactShadows, useAnimations, useFBX, useGLTF } from '@react-three/drei';
import { useWindowSize } from '@/hooks';

const file1 = 'animations/Idle.fbx';
const file2 = 'animations/Music.fbx';
const file3 = 'animations/Neutral.fbx';
const file4 = 'animations/OpenArms.fbx';

export const Ricardo = (props: any) => {
    const { nodes, materials } = useGLTF('models/ricardo_white4.glb');

    useGLTF.preload('models/ricardo_white4.glb');
    useFBX.preload(file1);
    useFBX.preload(file2);
    useFBX.preload(file3);
    useFBX.preload(file4);

    const [animation, setAnimation] = useState('idle');
    const [time, setTime] = useState<any>(null);
    const [fadeoutTime, setFadeoutTime] = useState<any>(null);
    const group = useRef();
    const { isSmartphone, isTablet, isHorizontal, isLaptop } = useWindowSize();

    const { animations: idleAnimation } = useFBX(file1);
    const { animations: musicAnimation } = useFBX(file2);
    const { animations: neutralAnimation } = useFBX(file3);
    const { animations: openArmsAnimation } = useFBX(file4);

    idleAnimation[0].name = 'idle';
    musicAnimation[0].name = 'music';
    neutralAnimation[0].name = 'neutral';
    openArmsAnimation[0].name = 'openarms';

    const { actions } = useAnimations(
        [idleAnimation[0], musicAnimation[0], neutralAnimation[0], openArmsAnimation[0]],
        group,
    );

    useEffect(() => {
        const item: any = actions[animation];

        if (item) {
            clearTimeout(time);
            clearTimeout(fadeoutTime);

            const next =
                animation === 'idle'
                    ? 'music'
                    : animation === 'music'
                    ? 'openarms'
                    : animation === 'openarms'
                    ? 'neutral'
                    : 'idle';

            const debounce = setTimeout(() => setAnimation(next), item._clip.duration * 1000);
            const fadeout = setTimeout(
                () => actions[animation]?.fadeOut(2.5),
                item._clip.duration * 1000 - 2500,
            );

            setTime(debounce);
            setFadeoutTime(fadeout);
        }
    }, [animation]);

    useEffect(() => {
        actions[animation]?.fadeIn(2.5).play();
        return () => actions[animation]?.fadeOut(2.5);
    }, [actions]);

    return (
        <group {...props} dispose={null} ref={group}>
            <group
                position={
                    isLaptop && isHorizontal
                        ? [0, -Math.PI / 2, 0]
                        : isSmartphone || isTablet
                        ? [0, -Math.PI / 3.25, 0]
                        : [0, -Math.PI / 2, 0]
                }
            >
                <skinnedMesh
                    name="EyeLeft"
                    geometry={nodes.EyeLeft.geometry}
                    material={materials.Wolf3D_Eye}
                    skeleton={nodes.EyeLeft.skeleton}
                    morphTargetDictionary={nodes.EyeLeft.morphTargetDictionary}
                    morphTargetInfluences={nodes.EyeLeft.morphTargetInfluences}
                />
                <skinnedMesh
                    name="EyeRight"
                    geometry={nodes.EyeRight.geometry}
                    material={materials.Wolf3D_Eye}
                    skeleton={nodes.EyeRight.skeleton}
                    morphTargetDictionary={nodes.EyeRight.morphTargetDictionary}
                    morphTargetInfluences={nodes.EyeRight.morphTargetInfluences}
                />
                <skinnedMesh
                    name="Wolf3D_Beard"
                    geometry={nodes.Wolf3D_Beard.geometry}
                    material={materials.Wolf3D_Beard}
                    skeleton={nodes.Wolf3D_Beard.skeleton}
                    morphTargetDictionary={nodes.Wolf3D_Beard.morphTargetDictionary}
                    morphTargetInfluences={nodes.Wolf3D_Beard.morphTargetInfluences}
                />
                <skinnedMesh
                    name="Wolf3D_Body"
                    geometry={nodes.Wolf3D_Body.geometry}
                    material={materials.Wolf3D_Body}
                    skeleton={nodes.Wolf3D_Body.skeleton}
                    morphTargetDictionary={nodes.Wolf3D_Body.morphTargetDictionary}
                    morphTargetInfluences={nodes.Wolf3D_Body.morphTargetInfluences}
                />
                <skinnedMesh
                    geometry={nodes.Wolf3D_Glasses.geometry}
                    material={materials.Wolf3D_Glasses}
                    skeleton={nodes.Wolf3D_Glasses.skeleton}
                />
                <skinnedMesh
                    geometry={nodes.Wolf3D_Hair.geometry}
                    material={materials.Wolf3D_Hair}
                    skeleton={nodes.Wolf3D_Hair.skeleton}
                />
                <skinnedMesh
                    name="Wolf3D_Head"
                    geometry={nodes.Wolf3D_Head.geometry}
                    material={materials.Wolf3D_Skin}
                    skeleton={nodes.Wolf3D_Head.skeleton}
                    morphTargetDictionary={nodes.Wolf3D_Head.morphTargetDictionary}
                    morphTargetInfluences={nodes.Wolf3D_Head.morphTargetInfluences}
                />
                <skinnedMesh
                    name="Wolf3D_Outfit_Bottom"
                    geometry={nodes.Wolf3D_Outfit_Bottom.geometry}
                    material={materials.Wolf3D_Outfit_Bottom}
                    skeleton={nodes.Wolf3D_Outfit_Bottom.skeleton}
                    morphTargetDictionary={nodes.Wolf3D_Outfit_Bottom.morphTargetDictionary}
                    morphTargetInfluences={nodes.Wolf3D_Outfit_Bottom.morphTargetInfluences}
                />
                <skinnedMesh
                    name="Wolf3D_Outfit_Footwear"
                    geometry={nodes.Wolf3D_Outfit_Footwear.geometry}
                    material={materials.Wolf3D_Outfit_Footwear}
                    skeleton={nodes.Wolf3D_Outfit_Footwear.skeleton}
                    morphTargetDictionary={nodes.Wolf3D_Outfit_Footwear.morphTargetDictionary}
                    morphTargetInfluences={nodes.Wolf3D_Outfit_Footwear.morphTargetInfluences}
                />
                <skinnedMesh
                    name="Wolf3D_Outfit_Top"
                    geometry={nodes.Wolf3D_Outfit_Top.geometry}
                    material={materials.Wolf3D_Outfit_Top}
                    skeleton={nodes.Wolf3D_Outfit_Top.skeleton}
                    morphTargetDictionary={nodes.Wolf3D_Outfit_Top.morphTargetDictionary}
                    morphTargetInfluences={nodes.Wolf3D_Outfit_Top.morphTargetInfluences}
                />
                <skinnedMesh
                    name="Wolf3D_Teeth"
                    geometry={nodes.Wolf3D_Teeth.geometry}
                    material={materials.Wolf3D_Teeth}
                    skeleton={nodes.Wolf3D_Teeth.skeleton}
                    morphTargetDictionary={nodes.Wolf3D_Teeth.morphTargetDictionary}
                    morphTargetInfluences={nodes.Wolf3D_Teeth.morphTargetInfluences}
                />
                <primitive object={nodes.Hips} />
                <ContactShadows
                    opacity={0.3}
                    scale={10}
                    blur={1}
                    far={10}
                    resolution={256}
                    color="#000000"
                />
            </group>
        </group>
    );
};
