import { Html } from '@react-three/drei';
import { motion, AnimatePresence } from 'framer-motion';
import { Skill } from '@/components';
import { skills } from '@/content';
import { useWindowSize } from '@/hooks';
import { activeSkill, openModal, setActiveSkill, scene } from '@/store';
import './index.scss';

export const Skills = () => {
    const { width, height } = useWindowSize();

    const handleModal = (skill: SkillItem) => {
        openModal(<Skill skill={skill} />);
        setActiveSkill(skill.name);
    };

    return (
        <Html wrapperClass="skills content" as="section">
            <AnimatePresence>
                {scene.value === 'skills' && (
                    <motion.ul
                        className="cropper"
                        initial={{ opacity: 0, y: -height / 3 }}
                        animate={{ opacity: 1, y: 0 }}
                        exit={{ opacity: 0, y: height / 3 }}
                        transition={{ duration: 0.25, ease: 'easeInOut' }}
                    >
                        {skills.map((skill, index) => (
                            <motion.li
                                key={skill.name}
                                onClick={() => handleModal(skill)}
                                variants={{
                                    hidden: { opacity: 0, x: width / 3 },
                                    visible: { opacity: 1, x: 0 },
                                }}
                                initial="hidden"
                                animate="visible"
                                transition={{
                                    duration: 0.15,
                                    delay: 0.25 + index * 0.015,
                                    ease: 'easeOut',
                                }}
                                title={skill.name}
                            >
                                <img
                                    src={`/logos/${skill.icon}.png`}
                                    alt={`${skill.name} logo`}
                                    className={activeSkill.value !== skill.name ? 'ns' : ''}
                                />
                            </motion.li>
                        ))}
                    </motion.ul>
                )}
            </AnimatePresence>
        </Html>
    );
};
