import { Html } from '@react-three/drei';
import { motion, AnimatePresence } from 'framer-motion';
import { Job } from '@/components';
import { career } from '@/content';
import { useWindowSize } from '@/hooks';
import { openModal, setModalContent, scene } from '@/store';
import './index.scss';

export const Career = () => {
    const { width, height } = useWindowSize();

    const handleModal = (job: CarrerItem) => {
        openModal(<Job job={job} />);
    };

    return (
        <Html wrapperClass="career content" as="section">
            <AnimatePresence>
                {scene.value === 'career' && (
                    <motion.ul
                        className="cropper"
                        initial={{ opacity: 0, y: -height / 3 }}
                        animate={{ opacity: 1, y: 0 }}
                        exit={{ opacity: 0, y: height / 3 }}
                        transition={{ duration: 0.25, ease: 'easeInOut' }}
                    >
                        {career.map((job, index) => (
                            <motion.li
                                key={job.company}
                                onClick={() => handleModal(job)}
                                variants={{
                                    hidden: { opacity: 0, x: width / 3 },
                                    visible: { opacity: 1, x: 0 },
                                }}
                                initial="hidden"
                                animate="visible"
                                transition={{
                                    duration: 0.35,
                                    delay: 0.25 + index * 0.05,
                                    ease: 'easeOut',
                                }}
                            >
                                <h3>
                                    {job.period}
                                    <b>{` ${job.company}`}</b>
                                </h3>
                                <p>{job.roles}</p>
                            </motion.li>
                        ))}
                    </motion.ul>
                )}
            </AnimatePresence>
        </Html>
    );
};
