import { useEffect } from 'react';
import { createPortal } from 'react-dom';
import { AnimatePresence, motion } from 'framer-motion';
import { closeModal, isModalVisible, modalContent, scene } from '@/store';
import { Icon } from '@/components';
import { useWindowSize } from '@/hooks';
import './index.scss';

const modalRoot: any = document.getElementById('modal');

export const Modal = () => {
    const { width, height, isSmartphone, isTablet } = useWindowSize();

    useEffect(() => {
        closeModal();
    }, [scene.value]);

    return createPortal(
        <AnimatePresence>
            {isModalVisible.value && (
                <motion.section
                    className="modal"
                    initial={
                        isSmartphone || isTablet
                            ? {
                                  x: width,
                                  opacity: 0,
                              }
                            : {
                                  x: width,
                                  y: -height / 7,
                                  scale: 2,
                                  rotate: -30,
                                  opacity: 0,
                              }
                    }
                    animate={
                        isSmartphone || isTablet
                            ? {
                                  x: 0,
                                  opacity: 1,
                                  transition: { duration: 0.3, ease: 'easeOut' },
                              }
                            : {
                                  x: 0,
                                  y: 0,
                                  scale: 1,
                                  rotate: 0,
                                  opacity: 1,
                                  transition: { duration: 0.3, ease: 'easeOut' },
                              }
                    }
                    exit={
                        isSmartphone || isTablet
                            ? {
                                  x: width,
                                  opacity: 0,
                                  transition: { duration: 0.5, ease: 'easeIn' },
                              }
                            : {
                                  x: width,
                                  y: -height / 7,
                                  scale: 2,
                                  rotate: -30,
                                  opacity: 0,
                                  transition: { duration: 0.75, ease: 'easeIn' },
                              }
                    }
                >
                    <button title="close" type="button" onClick={closeModal}>
                        <Icon name="cross" />
                    </button>
                    <div className="modal-content">{modalContent}</div>
                </motion.section>
            )}
        </AnimatePresence>,
        modalRoot,
    );
};
