import { useWindowSize } from '@/hooks';
import '../Job/index.scss';

interface SkillProps {
    skill: SkillItem;
}

export const Skill = ({ skill }: SkillProps) => {
    const { name, icon, projects, since, text } = skill;
    const { isSmartphone, isTablet } = useWindowSize();

    return (
        <section className="job">
            {isSmartphone || isTablet ? (
                <div className="job-head">
                    <img src={`/logos/${icon}.png`} alt={`${name} logo`} />
                    <h2>{name}</h2>
                </div>
            ) : (
                <>
                    <img src={`/logos/${icon}.png`} alt={`${name} logo`} />
                    <h2>{name}</h2>
                </>
            )}
            <div className="job-item">
                <h4>Since:</h4>
                <span>{since}</span>
            </div>
            <div className="job-item">
                <h4>Number of projects:</h4>
                <span>{projects}</span>
            </div>
            <p>{text}</p>
        </section>
    );
};
