import { scene } from '@/store';
import './index.scss';
import { AnimatePresence, motion } from 'framer-motion';

export const Badges = () => (
    <AnimatePresence>
        {scene.value === 'home' && (
            <motion.div className="badges">
                <motion.span
                    variants={{
                        hidden: {
                            height: 0,
                        },
                        visible: {
                            height: '2.9rem',
                            transition: { delay: 0.4, duration: 0.35, ease: 'easeInOut' },
                        },
                        exit: {
                            height: 0,
                            transition: { delay: 0, duration: 0.35, ease: 'easeInOut' },
                        },
                    }}
                    initial="hidden"
                    animate="visible"
                    exit="exit"
                />
                <motion.p
                    initial={{ x: 200 }}
                    animate={{ x: 0 }}
                    exit={{ x: 200 }}
                    transition={{ delay: 0.25, duration: 0.25, ease: 'easeInOut' }}
                >
                    <b>FRONT</b>
                    <br />
                    END
                </motion.p>
                <motion.p
                    initial={{ x: 200 }}
                    animate={{ x: 0 }}
                    exit={{ x: 200 }}
                    transition={{ delay: 0.35, duration: 0.25, ease: 'easeInOut' }}
                >
                    <b>BACK</b>
                    <br />
                    END
                </motion.p>
                <motion.p
                    initial={{ x: 200 }}
                    animate={{ x: 0 }}
                    exit={{ x: 200 }}
                    transition={{ delay: 0.45, duration: 0.25, ease: 'easeInOut' }}
                >
                    <b>DESIGN</b>
                    <br />
                    UI/UX
                </motion.p>
            </motion.div>
        )}
    </AnimatePresence>
);
