import { paths } from './paths';

export interface IconProps {
    color?: string;
    name: keyof typeof paths;
}

export const Icon = ({ color = '--color-base1000', name }: IconProps) => {
    const path = paths[name];

    return (
        <svg
            className="icon"
            width={30}
            height={30}
            viewBox="0 0 30 30"
            xmlns="http://www.w3.org/2000/svg"
            fill={`var(${color}`}
        >
            <path fillRule="evenodd" clipRule="evenodd" d={path} fill="inherit" />
        </svg>
    );
};
