import { navLinks, socialLinks } from '@/content';
import { Icon, Logo } from '@/components';
import { scene, setScene } from '@/store';
import './index.scss';

export const Sidebar = () => (
    <aside>
        <button className="home-button">
            <Logo onClick={() => setScene('home')} />
        </button>
        <nav>
            {navLinks.map((link) => (
                <button
                    className={scene.value === link ? 'active' : ''}
                    key={link}
                    onClick={() => setScene(link)}
                    title={link}
                >
                    {link}
                </button>
            ))}
        </nav>
        <div className="social">
            {socialLinks.map(({ icon, link }) => (
                <a href={link} rel="noreferrer" target="_blank" key={icon}>
                    <Icon name={icon} />
                </a>
            ))}
        </div>
    </aside>
);
